import React from 'react';
import {Switch, Route, Redirect} from 'react-router-dom';
import { layout } from 'antd';
import menus from 'src/Layout/LeftComponent/menu';
const { Content } = layout;

// 行为画像
const Quntihuaxiang = React.lazy(() => import ('./XingWeiHuaXiang/QunTiHuaXiang'));
const Gerenhuaxiang = React.lazy(() => import ('./XingWeiHuaXiang/GeRenHuaXiang'));
const GerenhuaxiangDetail = React.lazy(() => import ('./XingWeiHuaXiang/GeRenHuaXiang/Detail'));
// 综合预警
const YuJingLieBiao = React.lazy(() => import ('./ZongHeYuJing/YuJingLieBiao'));
const ZongTiGaiKuang = React.lazy(() => import ('./ZongHeYuJing/YuJingLieBiao/ZongTiGaiKuang'));
const YuJingDetail = React.lazy(() => import ('./ZongHeYuJing/YuJingLieBiao/Detail'));
const YuJingPeiZhi = React.lazy(() => import ('./ZongHeYuJing/YuJingPeiZhi'));
const YuJingPeiZhiAdd = React.lazy(() => import ('./ZongHeYuJing/YuJingPeiZhi/Add'));
const YuJingPeiZhiEdit = React.lazy(() => import ('./ZongHeYuJing/YuJingPeiZhi/Edit'));
const YuJingBaiMingDanPeiZhi = React.lazy(() => import ('./ZongHeYuJing/YuJingBaiMingDanPeiZhi'));
const GeRenAddOrEdit = React.lazy(() => import ('./ZongHeYuJing/YuJingBaiMingDanPeiZhi/GeRen/AddOrEdit'));
const JieJiaRiAddOrEdit = React.lazy(() => import ('./ZongHeYuJing/YuJingBaiMingDanPeiZhi/JieJiaRi/AddOrEdit'));
// 智慧资助
const PinKunShengFenXi = React.lazy(() => import ('./ZhiHuiZiZhu/PinKunShengFenXi'));
const PinKunShengFuZhuRenDing = React.lazy(() => import ('./ZhiHuiZiZhu/PinKunShengFuZhuRenDing'));
const PinKunShengFuZhuRenDingDetail = React.lazy(() => import('./ZhiHuiZiZhu/PinKunShengFuZhuRenDing/Detail'));
const PinKunShengChaXun = React.lazy(() => import ('./ZhiHuiZiZhu/PinKunShengChaXun'));
const PinKunShengChaXunDetail = React.lazy(() => import('./ZhiHuiZiZhu/PinKunShengChaXun/Detail'));
const YiChangPinKunSheng = React.lazy(() => import ('./ZhiHuiZiZhu/YiChangPinKunSheng'));
const YiChangPinKunShengDetail = React.lazy(() => import('./ZhiHuiZiZhu/YiChangPinKunSheng/Detail'))
const JianYiGuanAiXueSheng = React.lazy(() => import ('./ZhiHuiZiZhu/JianYiGuanAiXueSheng'));
const JianYiGuanAiXueShengDetail = React.lazy(() => import('./ZhiHuiZiZhu/JianYiGuanAiXueSheng/Detail'))
const TeBieGuanZhuXueSheng = React.lazy(() => import ('./ZhiHuiZiZhu/TeBieGuanZhuXueSheng'));
const TeBieGuanZhuXueShengDetail = React.lazy(() => import('./ZhiHuiZiZhu/TeBieGuanZhuXueSheng/Detail'))
// 教职工信息
const GeRenXinXi = React.lazy(() => import ('./JiaoZhiGongXinXi/GeRenXinXi'));
const GeRenXinXiDetail = React.lazy(() => import ('./JiaoZhiGongXinXi/GeRenXinXi/Detail'));
const ZongHeFenXi = React.lazy(() => import ('./JiaoZhiGongXinXi/ZongHeFenXi'));
// 财产资产
const CaiChanZiChan = React.lazy(() => import ('./CaiChanZiChan'));
// 后勤服务
const HouQinFuWu = React.lazy(() => import ('./HouQinFuWu'));
// 系统设置
const ZhangHuGuanLi = React.lazy(() => import ('./XiTongSheZhi/ZhangHuGuanLi'));
const JueSeQuanXianGuanLi = React.lazy(() => import ('./XiTongSheZhi/JueSeQuanXianGuanLi'));
const CaiDanGuanLi = React.lazy(() => import ('./XiTongSheZhi/CaiDanGuanLi'));

export default class Index extends React.Component {
    render(){
        return (
            <Content>
                <React.Suspense fallback={null}>
                    <Switch>
                        {/* 行为画像 */}
                        <Route exact path="/main/xingweihuaxiang/quntihuaxiang" component={Quntihuaxiang} />
                        <Route exact path="/main/xingweihuaxiang/gerenhuaxiang" component={Gerenhuaxiang} />
                        <Route exact path="/main/xingweihuaxiang/gerenhuaxiang/detail" component={GerenhuaxiangDetail} />

                        {/* 综合预警 */}
                        <Route exact path="/main/zongheyujing/yujingliebiao" component={YuJingLieBiao} />
                        <Route exact path="/main/zongheyujing/yujingliebiao/zongtigaikuang" component={ZongTiGaiKuang} />
                        <Route exact path="/main/zongheyujing/yujingliebiao/detail/:id" component={YuJingDetail} />
                        <Route exact path="/main/zongheyujing/yujingpeizhi" component={YuJingPeiZhi} />
                        <Route exact path="/main/zongheyujing/yujingpeizhi/add" component={YuJingPeiZhiAdd} />
                        <Route exact path="/main/zongheyujing/yujingpeizhi/edit/:id" component={YuJingPeiZhiEdit} />
                        <Route exact path="/main/zongheyujing/yujingbaimingdanpeizhi" component={YuJingBaiMingDanPeiZhi} />
                        <Route exact path="/main/zongheyujing/yujingbaimingdanpeizhi/geren/addoredit/:id" component={GeRenAddOrEdit} />
                        <Route exact path="/main/zongheyujing/yujingbaimingdanpeizhi/jiejiari/addoredit/:id" component={JieJiaRiAddOrEdit} />

                        {/* 智慧资助 */}
                        <Route exact path="/main/zhihuizizhu/pinkunshengfenxi" component={PinKunShengFenXi} />
                        <Route exact path="/main/zhihuizizhu/pinkunshengfuzhurending" component={PinKunShengFuZhuRenDing} />
                        <Route exact path="/main/zhihuizizhu/pinkunshengfuzhurending/detail" component={PinKunShengFuZhuRenDingDetail} />
                        <Route exact path="/main/zhihuizizhu/pinkunshengchaxun" component={PinKunShengChaXun} />
                        <Route exact path="/main/zhihuizizhu/pinkunshengchaxun/detail" component={PinKunShengChaXunDetail} />
                        <Route exact path="/main/zhihuizizhu/yichangpinkunsheng" component={YiChangPinKunSheng} />
                        <Route exact path="/main/zhihuizizhu/yichangpinkunsheng/detail" component={YiChangPinKunShengDetail} />
                        <Route exact path="/main/zhihuizizhu/jianyiguanaixuesheng" component={JianYiGuanAiXueSheng} />
                        <Route exact path="/main/zhihuizizhu/jianyiguanaixuesheng/detail" component={JianYiGuanAiXueShengDetail} />
                        <Route exact path="/main/zhihuizizhu/tebieguanzhuxuesheng" component={TeBieGuanZhuXueSheng} />
                        <Route exact path="/main/zhihuizizhu/tebieguanzhuxuesheng/detail" component={TeBieGuanZhuXueShengDetail} />

                        {/* 教职工信息 */}
                        <Route exact path="/main/jiaozhigongxinxi/gerenxinxi" component={GeRenXinXi} />
                        <Route exact path="/main/jiaozhigongxinxi/gerenxinxi/detail" component={GeRenXinXiDetail} />
                        <Route exact path="/main/jiaozhigongxinxi/zonghefenxi" component={ZongHeFenXi} />

                        {/* 财产资产 */}
                        <Route exact path="/main/caichanzichan" component={CaiChanZiChan} />

                        {/* 后勤服务 */}
                        <Route exact path="/main/houqinfuwu" component={HouQinFuWu} />

                        {/* 系统设置 */}
                        <Route exact path="/main/xitongshezhi/zhanghuguanli" component={ZhangHuGuanLi} />
                        <Route exact path="/main/xitongshezhi/juesequanxianguanli" component={JueSeQuanXianGuanLi} />
                        <Route exact path="/main/xitongshezhi/caidanguanli" component={CaiDanGuanLi} />

                        <Route path="*" render={(props) => <Redirect to={(menus[0].subMenus && menus[0].subMenus[0]) ? menus[0].subMenus[0].url : menus[0].url}/>} />
                    </Switch>
                </React.Suspense>
            </Content>
        )
    }
}